export const getHazardCodeFromUnitCode = (unitCode) => {
  console.log('unitCode', unitCode);
  return unitCode.slice(-3).toUpperCase();
}

export const getLidarFeatureName = (projectName, areaName) => {
  if (areaName) {
    return `${projectName} - ${areaName}`;
  }

  return projectName;
}
